.listBtn ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.listBtn ul li {
    --c: #000;
    color: var(--c);
    font-size: 16px;
    border: 0.3em solid var(--c);
    border-radius: 0.5em;
    width: 12em;
    height: 3em;
    text-transform: uppercase;
    font-weight: bold;
    font-family: sans-serif;
    letter-spacing: 0.1em;
    text-align: center;
    line-height: 3em;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: 0.5s;
    margin: 1em;
    cursor: pointer;

}

.listBtn ul li span {
    position: absolute;
    width: 25%;
    height: 100%;
    background-color: var(--c);
    transform: translateY(150%);
    border-radius: 50%;
    left: calc((var(--n) - 1) * 25%);
    transition: 0.5s;
    transition-delay: calc((var(--n) - 1) * 0.1s);
    z-index: -1;
}

.listBtn ul li:hover {
    color: white;
}

.listBtn ul li:hover span {
    transform: translateY(0) scale(2);
}

.listBtn ul li span:nth-child(1) {
    --n: 1;
}

.listBtn ul li span:nth-child(2) {
    --n: 2;
}

.listBtn ul li span:nth-child(3) {
    --n: 3;
}

.listBtn ul li span:nth-child(4) {
    --n: 4;
}

html {
    scroll-behavior: smooth;
}


.btn-product.listBtn ul li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10em;
    height: 45px;
}

.btn-product-2.listBtn ul li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10em;
    height: 35px;
}

.checkout.listBtn ul li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 15em;
    height: 45px;
}
.active{
    padding-bottom: 5px;
    border-bottom: 3px solid black;
}